import * as React from 'react'
import { useState, useEffect } from 'react';
import { useNavigate, useLocation } from "react-router";
import DatePicker from "react-datepicker"
import { ko } from "date-fns/esm/locale";
import 'react-datepicker/dist/react-datepicker.css';
import styles from "./Update.module.css"
import axios from 'axios';





const Update = () => {
  const { state } = useLocation();
  const Navigate = useNavigate();





            //   
  const goBack = () => {
    const yesOrNo =  window.confirm("    .  ?")
    if(yesOrNo){
      Navigate(-1)
    } 
  }


          //  DatePicker 
    const [startDate, setStartDate] = useState(new Date());


          //    value.
    const [ inputTitle, setTitle] = useState(state.title);
    const [ inputType, setType] = useState(state.type);
    const [ inputPlace, setPlace] = useState(state.place);
    const [ inputText, setText] = useState(state.text);
    const [ inputTeamText, setTeamText] = useState(state.teamText);
    const [ inputTeamName, setTeamName] = useState(state.teamName);

    const handleTitle = (e) => {
          setTitle(e.target.value);
    };

    const handleType = (e) => {
      setType(e.target.value);
    };  


    const handlePlace = (e) => {
      setPlace(e.target.value);
    };  

    const handleText = (e) => {
      setText(e.target.value);
    };  

    const handleTeamText = (e) => {
      setTeamText(e.target.value);
    };  



    const handleTeamName = (e) => {
      setTeamName(e.target.value);
    };  



    const handleClick = () => {
        alert(" !")
        submit()
        Navigate('/About')
      }
    

    const submit = async() => {
      let body = {
        title: inputTitle,
        type: inputType,
        date: startDate,
        place: inputPlace,
        text: inputText,
        teamText: inputTeamText,
        teamName: inputTeamName,
        num: state.num
      };
      await axios({
        method : 'post',
        url: 'http://localhost:5000/Detail/update',
        data: body
      })
    }


  return(
    <div className={styles.Body}>
      <button type="button" onClick={goBack}></button>
      <table className={styles.Table}>
        <thead>
          <tr>
            <th colSpan="3">
              <input type="text" value={inputTitle} onChange={handleTitle} placeholder=' '/>
              </th>
          </tr>
        </thead>
        <tbody>
          <tr>
            <td></td>
            <td></td>
            <td></td>
          </tr>
          <tr>
            <td>
              <select value={inputType} onChange={handleType}>
                <option value=""></option>
                <option value=""></option>
                <option value=""></option>
                <option value=""></option>
                <option value=""></option>
                <option value=""></option>
                <option value=""></option>
                <option value=""></option>
                <option value=""></option>
              </select>
            </td>
            <td>
            <DatePicker
            locale={ko}
      dateFormat="yyyy/MM/dd"
      selected={startDate}
      onChange={(date) => setStartDate(date)}
    />
            </td>
            <td>
            <select value={inputPlace} onChange={handlePlace}>
                <option value=""></option>
                <option value=""></option>
                <option value=""></option>
                <option value=""></option>
                <option value=""></option>
                <option value=""></option>
                <option value=""></option>
                <option value=""></option>
                <option value=""></option>
                <option value=""></option>
                <option value=""></option>
              </select>
            </td>
          </tr>
          <tr>
            <td colSpan="3">
              <input type="text" value={inputText} onChange={handleText} placeholder='  ' />
            </td>
          </tr>
          <tr>
            <td colSpan="2">
              <input value={inputTeamText} onChange={handleTeamText}  placeholder='  !' />
            </td>
            <td colSpan="1">
              <input value={inputTeamName} onChange={handleTeamName} placeholder='   !' />
            </td>
          </tr>
        </tbody>
      </table>
      <button onClick={handleClick}> !</button>
    </div>
  )
}

export default Update