import * as React from 'react'
import { useState } from 'react';
import { useNavigate } from "react-router";
import DatePicker from "react-datepicker"
import { ko } from "date-fns/esm/locale";
import 'react-datepicker/dist/react-datepicker.css';
import styles from "./Create.module.css"
import axios from 'axios';





const Create = () => {
  const Navigate = useNavigate();
  let sessionStorage = window.sessionStorage;
  let userId = sessionStorage.getItem('loginId');





            //   
  const goBack = () => {
    const yesOrNo =  window.confirm("    .  ?")
    if(yesOrNo){
      Navigate(-1)
    } 
  }


          //  DatePicker 
    const [startDate, setStartDate] = useState(new Date());


          //    value.
    const [ inputTitle, setTitle] = useState("");
    const [ inputType, setType] = useState("");
    const [ inputPlace, setPlace] = useState("");
    const [ inputText, setText] = useState("");
    const [ inputTeamText, setTeamText] = useState("");
    const [ inputTeamName, setTeamName] = useState("");

    const handleTitle = (e) => {
          setTitle(e.target.value);
    };

    const handleType = (e) => {
      setType(e.target.value);
    };  


    const handlePlace = (e) => {
      setPlace(e.target.value);
    };  

    const handleText = (e) => {
      setText(e.target.value);
    };  

    const handleTeamText = (e) => {
      setTeamText(e.target.value);
    };  



    const handleTeamName = (e) => {
      setTeamName(e.target.value);
    };  



    const handleClick = async(e) => {
      if(inputTitle === "" && inputTeamName === "" && inputText === ""){
        alert("        ")
        Navigate(-1)
      } else {
        alert(" !")
        submit()
        Navigate(-1)
      }
    }

    const submit = async() => {
      let body = {
        title: inputTitle,
        type: inputType,
        date: startDate,
        place: inputPlace,
        text: inputText,
        teamText: inputTeamText,
        teamName: inputTeamName,
        id: userId
      };
      await axios({
        method : 'post',
        url: 'http://localhost:5000/Detail/create',
        data: body
      }).then((res)=>{
        console.log(res.data)
      })
    }



  return(
    <div className={styles.Body}>
      <button type="button" onClick={goBack} className={styles.backBtn}></button>
      <table className={styles.Table}>
        <thead>
          <tr>
            <th colSpan="3">
              <input type="text" value={inputTitle} onChange={handleTitle} placeholder=' '/>
              </th>
          </tr>
        </thead>
        <tbody>
          <tr>
            <td></td>
            <td></td>
            <td></td>
          </tr>
          <tr>
            <td>
              <select value={inputType} onChange={handleType}>
                <option value=""></option>
                <option value=""></option>
                <option value=""></option>
                <option value=""></option>
                <option value=""></option>
                <option value=""></option>
                <option value=""></option>
                <option value=""></option>
                <option value=""></option>
              </select>
            </td>
            <td>
            <DatePicker
            locale={ko}
      dateFormat="yyyy/MM/dd"
      selected={startDate}
      onChange={(date) => setStartDate(date)}
    />
            </td>
            <td>
            <select value={inputPlace} onChange={handlePlace}>
                <option value=""></option>
                <option value=""></option>
                <option value=""></option>
                <option value=""></option>
                <option value=""></option>
                <option value=""></option>
                <option value=""></option>
                <option value=""></option>
                <option value=""></option>
                <option value=""></option>
                <option value=""></option>
              </select>
            </td>
          </tr>
          <tr>
            <td colSpan="3" className={styles.textTd}>
              <textarea type="text" value={inputText} onChange={handleText} placeholder='  ' />
            </td>
          </tr>
          <tr>
            <td colSpan="2">
              <textarea value={inputTeamText} onChange={handleTeamText}  placeholder='  !' />
            </td>
            <td colSpan="1">
              <input value={inputTeamName} onChange={handleTeamName} placeholder='   !' className={styles.teamName} />
            </td>
          </tr>
        </tbody>
      </table>
      <button onClick={handleClick} className={styles.btn}> !</button>
    </div>
  )
}

export default Create